import headerNav from '@/components/header_nav.vue'
export default {
	data() {
		return {
			title: '车险凭证',
			icon:'../../static/icon/left_whitch.png',
			url_le:'/pages/main/my',
			current:0,
			loadshow:true,
			page:0,
			pages:1,
			list:[]
			
		}
	},
	methods:{
		
		async loadings(){
			if(this.loadshow){
				var list =await this.$service.loadthepage({
					that:this,
					direct:false,
					url:this.$service.api.center.insuranceLog,
					method:'GET',
					data:{
						status:this.current
					}
				})
				if(list.data.data.length == 0){
					uni.showToast({
						title:'暂无数据',
						icon:'none'
					})
					this.loadshow = false;
					return
				}
				
				/* this.pages = list.data.data.last_page;
				this.page = list.data.data.current_page;*/
				this.list = [...list.data.data] 
			}
			
		},
		changeTabs(i){
			this.page = 0;
			this.pages = 1;
			this.loadshow　= true
			this.list = []
			this.current = i;
			this.loadings()
		},
		jump(url){
			uni.navigateTo({
				url
			})
		}
	},
	components:{
		headerNav
	},
	 onShow() {
		this.loadings()
	},
	onReachBottom() {
		// console.log('hfasduighfaseuih');
		if(this.loadshow){
			this.loadings()
		}
	}
}
